@import "./variables.scss";
@import "./animations.scss";

.home-main {
  background-color: $kaki;
  display: grid;
  padding: 1rem;
  & > * {
    animation: 600ms fadeIn;
    display: flex;
    place-self: center;
    margin: 1rem;
    border-radius: 15px;
  }
}

.welcome-container {
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.welcome-msg {
  font-size: 2rem;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 1rem;
  text-align: center;
  line-height: 2rem;
  font-weight: bold;
}

.shop-btn {
  border: none;
  background-color: $dark-blue;
  color: white;
  font-weight: bold;
  cursor: pointer;
  font-size: 2rem;
  padding: 0.5rem 1rem;
  border-radius: 50px;
  transition: 300ms;
  &:hover,
  &:focus {
    transform: scale(1.2);
    transition: 300ms;
  }
}

@media screen and (orientation: landscape) {
  .home-main {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
  }

  .welcome-container {
    grid-row: 1/2;
    grid-column: 1/2;
  }
}

@media screen and (max-height: 500px) {
  .home-main {
    grid-template-rows: 0.5fr;
    img {
      height: 100%;
    }
  }

  .welcome-msg {
    font-size: 1.3rem;
    padding: 0.5rem 0.62rem;
  }
}
